import {
		mapState
	} from 'vuex';
import md5 from '../../kayida/common/md5.js'

export default {
	computed: {
	            ...mapState(['userInfo'])  
	        },
	data() {
		return {
			audit_list:'',
			type:0,
			none:0
		}
	},
	methods: {
		
	},
	onLoad(option) {
		this.type = option.type
		let data = {
			// page: 1,
			type: option.type,
			user_id: this.userInfo.user_id,
			token: this.userInfo.token,
			sign: md5(this.userInfo.user_id+this.userInfo.token)
		}
		this.request.httpRequest('api/taskOrder/index',data,'GET').then(res=>{
			if(res.data.code === 1000){
				this.audit_list = res.data.retval
				if(this.audit_list.length < 1){
					this.none = 1
				}else{
					this.none = 0
				}
			}else{
				uni.showToast({
					title:res.data.msg,
					icon:'none'
				})
			}
		})
	}
}
